import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { FormControl, FormGroup, Validators } from "@angular/forms"
import { users } from 'src/app/dashboard/model/users/users.config';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  constructor(private app: AppService) { }
  ngOnInit() {
    this.loginForm = new FormGroup({
      username: new FormControl("", Validators.required),
      password: new FormControl("", Validators.required),
    })
  }
  login() {
    if (!this.loginForm.valid) {
      this.app.log("بعض البيانات مفقودة");
      return;
    }
    this.app.wait();
    this.app.http.post<users>("/api/login", this.loginForm.value).subscribe(
      res => {
        this.app.wait(false);
        if (Object.keys(res).length == 0) {
          this.app.log("من فضلك تاكد من بياناتك");
          return;
        }
        this.app.user = res;
        sessionStorage.setItem("lawerAssistant", JSON.stringify(res));
        this.app.router.navigate(["../"]);
      },
      err => { }
    )
  }
}
